import { mapState, mapActions } from 'vuex'

export default {
  mixins: [],
  data: function () {
    return {
      memoError: '',
      memoErrored: false
    }
  },
  mounted () {
  },
  watch: {
  },
  computed: {
    ...mapState([
      'localPost'
    ])
  },
  methods: {
    ...mapActions([
      'changeLocalPost'
    ]),
    setName (content) {
      if (content.length > 217) {
        this.memoError = 'Name is too long'
        this.memoErrored = true
      }
      const data = ['0x6d01', content]
      this.sendTransaction(data, [])
    },
    newPost (content) {
      if (content.length > 217) {
        this.memoError = 'Post is too long'
        this.memoErrored = true
      }
      const data = ['0x6d02', content]
      this.sendTransaction(data, [])

      // set local item
      let item = {}
      item.prefix = '6d02'
      item.sender = this.getAddress()
      item.parentTxId = null
      item.content1 = content
      item.timestamp = parseInt((new Date().getTime())/1000)
      this.changeLocalPost(item)
    },
    replyPost (txId, content) {
      if (content.length > 184) {
        this.memoError = 'Reply is too long'
        this.memoErrored = true
      }
      const data = ['0x6d03', '0x' + reverseHex(txId), content]
      this.sendTransaction(data, [])

      // set local item
      let item = {}
      item.prefix = '6d03'
      item.sender = this.getAddress()
      item.parentTxId = txId
      item.content2 = content
      item.timestamp = parseInt((new Date().getTime())/1000)
      this.changeLocalPost(item)
    },
    likePost (txId, address, amount) {
      let toArray = []
      if (amount > 0) {
        let toElement = {}
        toElement['address'] = address
        toElement['value'] = parseInt(amount)
        toArray.push(toElement)
      }
      const data = ['0x6d04', '0x' + reverseHex(txId)]
      this.sendTransaction(data, toArray)
    },
    setProfileText (content) {
      if (content.length > 217) {
        this.memoError = 'Profile text is too long'
        this.memoErrored = true
      }
      const data = ['0x6d05', content]
      this.sendTransaction(data, [])
    },
    follow (address) {
      console.log('follow:' + address)
      let legacyUserAddress
      if (bchaddr.isCashAddress(address)) {
        legacyUserAddress = bchaddr.toLegacyAddress(address)
      } else if (bchaddr.isLegacyAddress(address)) {
        legacyUserAddress = address
      }
      const bytes = bs58.decode(legacyUserAddress)
      const b = new Buffer(bytes.toString('hex').substr(2,40), 'hex')
      const data = ['0x6d06', '0x' + b.toString('hex')]
//      const address2 = bitcore.Address.fromPublicKeyHash(b).toString().substr(12)
      this.sendTransaction(data, [])
    },
    unfollow (address) {
      console.log('unfollow:' + address)
      let legacyUserAddress
      if (bchaddr.isCashAddress(address)) {
        legacyUserAddress = bchaddr.toLegacyAddress(address)
      } else if (bchaddr.isLegacyAddress(address)) {
        legacyUserAddress = address
      }
      const bytes = bs58.decode(legacyUserAddress)
      const b = new Buffer(bytes.toString('hex').substr(2,40), 'hex')
      const data = ['0x6d07', '0x' + b.toString('hex')]
      this.sendTransaction(data, [])
    },
    setProfilePicture(content) {
      if (content.length > 217) {
        this.memoError = 'Profile picture address is too long'
        this.memoErrored = true
      }
      const data = ['0x6d0a', content]
      this.sendTransaction(data, [])
    },
    timeSince(unix_timestamp) {
      const date = new Date(parseInt(unix_timestamp) * 1000)
      const seconds = Math.floor((new Date() - date) / 1000)
      let interval = Math.floor(seconds / 31536000)
      if (interval >= 1) {
        return this.unixTimeToDatetime(unix_timestamp).substr(0, 11)
      }
      interval = Math.floor(seconds / 2592000)
      if (interval >= 1) {
        return this.unixTimeToDatetime(unix_timestamp).substr(5, 6)
      }
      interval = Math.floor(seconds / 86400)
      if (interval >= 1) {
        return  this.unixTimeToDatetime(unix_timestamp).substr(5, 6)
      }
      interval = Math.floor(seconds / 3600)
      if (interval >= 1) {
        return interval + "h"
      }
      interval = Math.floor(seconds / 60)
      if (interval >= 1) {
        return interval + "m"
      }
      return Math.floor(seconds) + "s"
    },
    largeDatetime (unix_timestamp) {
      let a = new Date(parseInt(unix_timestamp) * 1000)
      let months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
      let year = a.getFullYear()
      let month = months[a.getMonth()]
      let date = a.getDate()
      let hour = ('0' + a.getHours()).substr(-2)
      let min = ('0' + a.getMinutes()).substr(-2)
      let time =   hour + ':' + min + ' - ' + date + ' ' + month + ' ' + year
      return time
    },
    unixTimeToDatetime (unix_timestamp) {
      let a = new Date(parseInt(unix_timestamp) * 1000)
      let months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
      let year = a.getFullYear()
      let month = months[a.getMonth()]
      let date = a.getDate()
      let hour = ('0' + a.getHours()).substr(-2)
      let min = ('0' + a.getMinutes()).substr(-2)
      let time = year + ' ' + month + ' ' + date + ' ' + hour + ':' + min
      return time
    }
  }
}



