<template>
    <div class="grid">
      <HeaderNav  v-bind:title="'PosterCoins Wallet Info'"/>

      <div class="s_results form">

        <p>
          Balance: {{ posterCoinsAmount }}
        </p>

        <p style="padding-bottom:20px;"></p>
      </div>
      <Footer/>
    </div>

</template>

<script>
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'
import actionsWalletMixin from '@/mixins/actionsWalletMixin'

export default {
  name: 'ActionsWalletInfo',
  mixins: [actionsWalletMixin],
  components: {
    HeaderNav,
    Footer
  },
  data () {
    return {
      posterCoins: null
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Receive PosterCoins'
      }
    }
  },
  mounted () {
    this.getActionsCount()
  },
  watch: {
  },
  methods: {
  }
}
</script>
<style>
.option-active {
  font-weight: bold;
}
</style>



