<template>
  <div class="grid grey">
    <HeaderNav/>

    <div class="s_left">
      <UserNav :address="address" />
    </div>

    <div  class="s_results"  v-infinite-scroll="loadMore" infinite-scroll-disabled="scrollLoading" infinite-scroll-distance="500">
      <p class="section-title">
        Notifications
      </p>
      <span v-if="loading === false">
        <span  v-if="resultBitdbItems.length > 0">
          <div class="stream-item" v-for="(item, index) in resultBitdbItems">
              <div class="stream-item-header">
                <router-link :to="'/user/' + item.sender" class="account-group">
                  <img class="avatar" v-if="profilePictures[item.sender]" :src="profilePictures[item.sender]" alt="">
                  <img class="avatar" v-else src="../../assets/images/profile-picture-normal.png" alt="">
                </router-link>
                    <span class="full-name-group">
                      <router-link :to="'/user/' + item.sender" class="account-group">
                      <strong class="full-name text-truncate" v-if="usernames[item.sender]">{{ usernames[item.sender] }}</strong>
                      <strong class="full-name text-truncate" v-else>{{ item.sender.substr(0, 12) }}...</strong>
                      </router-link>
                      <span class="user-badges">
                          <strong class="full-name text-truncate" v-if="followedUsers.includes(item.sender)"><i class="fas fa-star following"></i></strong>
                      </span>
                      <span v-if="item.prefix === '6d03'">
                      replied to your <a href="javascript:void(0)" @click="displayPost(item.parentTxId)">post</a>
                      </span>
                      <span v-if="item.prefix === '6d04'">
                      liked your <a href="javascript:void(0)" @click="displayPost(item.parentTxId)">post</a>
                      </span>
                      <span v-if="item.prefix === '6d06'">
                      follows you
                      </span>
                      <span>
                        <span class="date" v-if="item.timestamp">&nbsp;- {{ timeSince(item.timestamp) }}</span>
                        <span class="date" v-else>&nbsp;- now</span>
                      </span>
                    </span>
              </div>

              <div class="stream-item-text-container" @click="displayModal(item, 'displayModal')">
                <p class="item-text" v-if="item.prefix === '6d03'">{{ item.content2 }}</p>
                <p class="item-text" v-if="item.prefix === '6d04'">&nbsp;</p>
                <p class="item-text" v-if="item.prefix === '6d06'">&nbsp;</p>
              </div>
          </div>
        </span>
        <span v-else>
          <div class="n_results">No notifications</div>
        </span>
      </span>
      <span v-else>
      <div class="n_results" v-bind:class="{ 'preloader': !streamEnd }"></div>
      </span>
    </div>

    <div class="s_right topics">
    </div>

    <Footer/>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'
import UserNav from '@/components/User/Nav'

export default {
  name: 'UserNotifications',
  mixins: [],
  props: {
    address: String
  },
  data () {
    return {
      resultBitdbItems: [],
      offset: 0,
      page: 1,
      itemsPerPage: 20,
      loading: false,
      errored: false,
      streamEnd: false,
      scrollLoading: false
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Notifications'
      },
      // Meta tags
      meta: [
        { name: 'application-name', content: 'Poster.cash' },
      ]
    }
  },
  components: {
    HeaderNav,
    Footer,
    UserNav
  },
  mounted () {
    if (this.myTxIds.length > 0) {
      this.getNotifications(this.address)
    }
    this.changeNotificationChecked(parseInt((new Date().getTime())/1000))
  },
  created() {
  },
  computed: {
    ...mapState([
      'notificationChecked',
      'myTxIds',
      'crawledAddresses',
      'followedUsers',
      'profilePictures',
      'usernames'
    ])
  },
  watch: {
    myTxIds: function (val) {
      if (val.length > 0) {
        this.getNotifications(this.address)
      }
    }
  },
  methods: {
    ...mapActions([
      'changeCurrentPost',
      'changeNotificationChecked',
      'addressesUsernames',
      'addressesProfilePictures'
    ]),
    displayModal (item, modal) {
      let userAddress = item.sender
      let host = window.location.href.replace(window.location.pathname,'');
      sessionStorage.setItem('oldUrl', host + '/user/notifications/' + userAddress)
      sessionStorage.setItem('oldTitle', 'Notifications')
      this.changeCurrentPost(item)
      this.openModal(modal)
    },
    displayPost (txId) {
      this.errored = false
      let query = {
        'v': 3,
        'q': {
          'find': {
            '$and': [{
                'out.b0': {
                  'op': 106
                }
              }, {
                'tx.h': reverseHex(txId)
              }
            ]
          },
          'project': { 'in': 1, 'out.$': 1, 'blk': 1, 'tx': 1 },
          'skip': 0,
          'limit': 1
        },
        'r': {
          'f': '[ .[] | {prefix: .out[0].h1, sender: .in[0].e.a, parentTxId: .out[0].h2, content1: .out[0].s2, content2: .out[0].s3, timestamp: .blk.t?, txId: .tx.h} ]'
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = window.bitdbNode + b64

      let header = {
        headers: { key: window.bitdbApiKey }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            let results = []
            if (response.data.u !== undefined) {
              results = results.concat(response.data.u.reverse())
            }
            if (response.data.c !== undefined) {
              results = results.concat(response.data.c)
            }

            let item = results[0]
            this.displayModal(item, 'displayModal')
          }
        })
        .catch(error => {
          this.errored = true
        })
    },
    getNotifications (userAddress, offset = 0) {
      if (offset === 0) {
        this.loading = true
      }
      this.errored = false

      let legacyUserAddress
      if (bchaddr.isCashAddress(userAddress)) {
        legacyUserAddress = bchaddr.toLegacyAddress(userAddress)
      } else if (bchaddr.isLegacyAddress(userAddress)) {
        legacyUserAddress = userAddress
      }
      const bytes = bs58.decode(legacyUserAddress)
      const userAddressHex = bytes.toString('hex').substr(2,40)

      let followedSearch = {}
      followedSearch['$or'] = []
      for (let i = 0; i < this.myTxIds.length; ++i) {
        followedSearch['$or'].push({ 'out.h2': reverseHex(this.myTxIds[i]) })
      }

      let findQuery = {
        '$and': [
          {
            'out.b0': {
              'op': 106
            }
          },
          {
            '$or': [{
                '$and': [{
                    '$or': [{
                      'out.h1': '6d03'
                    }, {
                      'out.h1': '6d04'
                    }]
                  },
                  followedSearch
                ]
              },
              {
                '$and': [{
                    '$or': [{
                      'out.h1': '6d06'
                    }]
                  },
                  {
                    'out.h2': '' + userAddressHex + ''
                  },
                  {
                    'in.e.a': {'$ne': userAddress}
                  }
                ]
              }
            ]
          }
        ]
      }

      let query = {
        'v': 3,
        'q': {
          'find': findQuery,
          'project': { 'in': 1, 'out.$': 1, 'blk': 1, 'tx': 1 },
          'skip': parseInt(offset),
          'limit': this.itemsPerPage
        },
        'r': {
          'f': '[ .[] | {prefix: .out[0].h1, sender: .in[0].e.a, parentTxId: .out[0].h2, content1: .out[0].s2, content2: .out[0].s3, timestamp: .blk.t?, txId: .tx.h} ]'
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = window.bitdbNode + b64

      let header = {
        headers: { key: window.bitdbApiKey }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            let newBitdbItems = []
            if (response.data.u !== undefined) {
              newBitdbItems = newBitdbItems.concat(response.data.u.reverse())
            }
            if (response.data.c !== undefined) {
              newBitdbItems = newBitdbItems.concat(response.data.c)
            }

            // populate usernames and txIds
            let newAddresses = []
            if (this.getAddress()) {
              newAddresses.push(this.getAddress())
            }
            for (let i = 0; i < newBitdbItems.length; ++i) {
              if(newAddresses.indexOf(newBitdbItems[i].sender) === -1) {
                newAddresses.push(newBitdbItems[i].sender)
              }
            }
            let that = this
            newAddresses = newAddresses.filter( function( el ) {
              return that.crawledAddresses.indexOf( el ) < 0;
            } )

            this.addressesUsernames([ newAddresses ])
            this.addressesProfilePictures([ newAddresses ])
            this.resultBitdbItems = this.resultBitdbItems.concat(newBitdbItems)

            this.offset += this.itemsPerPage
            this.loading = false
            this.scrollLoading = false
          }
        })
        .catch(error => {
//          console.log(JSON.stringify(error))
          this.errored = true
          this.loading = false
        })
    },
    loadMore() {
      this.scrollLoading = true;
      if (this.streamEnd === false && this.errored === false && this.loading === false && this.myTxIds.length > 0) {
        this.getNotifications(this.address, this.offset)
      }
    }
  }
}
</script>

<style>
</style>



