<template>
  <div>
  	<p>Poster is social network client using the Memo protocol. It's powered by Bitdb and only communicates with a Bitdb node.</p>
    <p>Poster does not store any user data. All actions are performed in user's browser.</p>
    <p>Original memo protocol: <a href="https://memo.cash/protocol" target="_blank">https://memo.cash/protocol</a></p>
    <p><b>Implemented protocol:</b></p>
      <table>
        <tr>
          <th>Action</th>
          <th>Prefix</th>
          <th>Values</th>
          <th>Status</th>
        </tr>
        <tr>
          <td>Set name</td>
          <td>0x6d01</td>
          <td>name(217)</td>
          <td>Implemented</td>
        </tr>
        <tr>
          <td>Post memo</td>
          <td>0x6d02</td>
          <td>message(217)</td>
          <td>Implemented</td>
        </tr>
        <tr>
          <td>Reply to memo</td>
          <td>0x6d03</td>
          <td>txhash(32), message(184)</td>
          <td>Implemented</td>
        </tr>
        <tr>
          <td>Like / tip memo</td>
          <td>0x6d04</td>
          <td>txhash(32)</td>
          <td>Implemented</td>
        </tr>
        <tr>
          <td>Set profile text</td>
          <td>0x6d05</td>
          <td>message(217)</td>
          <td>Implemented</td>
        </tr>
        <tr>
          <td>Follow user</td>
          <td>0x6d06</td>
          <td>address(35)</td>
          <td>Implemented</td>
        </tr>
        <tr>
          <td>Unfollow user</td>
          <td>0x6d07</td>
          <td>address(35)</td>
          <td>Implemented</td>
        </tr>
        <tr>
          <td>Set profile picture</td>
          <td>0x6d0a</td>
          <td>url(217)</td>
          <td>Implemented</td>
        </tr>
        <tr>
          <td>Post topic message</td>
          <td>0x6d0c</td>
          <td>topic_name(variable), message(214 - topic length)</td>
          <td>Implemented</td>
        </tr>
      </table>
  </div>
</template>

<script>
  export default {
    name: 'About',
    components: {
    }
  };
</script>

<style>
</style>



