<template>
  <span>
        <li v-if="logged" style="float: right;"><a href="javascript:void(0)" @click="openModal('newModal')" class="button-active">Post</a></li>
     		<li v-if="logged" class="dropdown" style="float: right;">
          <i class="fas fa-user-circle user-nav-icon" @click="showProfileNav = !showProfileNav"></i>
            <ul v-bind:class="{ 'nav-dropdown': !showProfileNav, 'nav-dropdown-active': showProfileNav }">
              <li><a href="javascript:void(0)">Balance: {{ balance }} sat</a></li>
              <li><router-link to="/pages/editProfile">Edit profile</router-link></li>
              <li><router-link to="/wallet/receive">Receive {{ networkName || 'BSV' }}</router-link></li>
              <li><router-link to="/wallet/send">Send {{ networkName || 'BSV' }}</router-link></li>
              <li><router-link to="/wallet/export">Export wallet key</router-link></li>
              <li><a href="/" @click="logout">Logout</a></li>
            </ul>
        </li>
     		<li v-if="logged" style="float: right;">
          <router-link :to="'/user/notifications/' + getAddress()" class="nav-icon"><i class="fas fa-bell user-nav-icon" v-bind:class="{ 'user-nav-icon-active': notificationsCount }"><span class="badge" v-if="notificationsCount">{{ notificationsCount }}</span></i></router-link>
        </li>
     		<li v-else style="float: right;"><a href="javascript:void(0)" @click="openModal('loginModal')" class="button-active">Sign in</a></li>
  </span>
</template>


<script>
import { mapState, mapActions } from 'vuex'

  export default {
      name: 'UserNav',
      mixins: [],
      data () {
        return {
          networkName: window.networkName,
          showProfileNav: false,
          notificationsCount: 0
        }
      },
      props: {
      },
      mounted () {
        if (this.logged) {
          this.getAddressInfo()
          this.getNotificationCount()
        }
      },
      computed: {
        ...mapState([
          'notificationChecked',
          'myTxIds'
        ]),
        logged: function () {
          if (this.loggedViewMode() || this.loggedSpendMode()) {
            return true
          }
          return false
        },
        balance: function () {
          if (this.addressInfo) {
            return this.addressInfo.balanceSat
          }
          return 0
        }
      },
      watch: {
        myTxIds: function (val) {
          this.getNotificationCount()
        }
      },
      methods: {
        getNotificationCount () {
          if (this.myTxIds.length > 0) {
            let legacyUserAddress
            if (bchaddr.isCashAddress(this.getAddress())) {
              legacyUserAddress = bchaddr.toLegacyAddress(this.getAddress())
            } else if (bchaddr.isLegacyAddress(this.getAddress())) {
              legacyUserAddress = this.getAddress()
            }
            const bytes = bs58.decode(legacyUserAddress)
            const userAddressHex = bytes.toString('hex').substr(2,40)

            let followedSearch = {}
            followedSearch['$or'] = []
            for (let i = 0; i < this.myTxIds.length; ++i) {
              followedSearch['$or'].push({ 'out.h2': reverseHex(this.myTxIds[i]) })
            }

            let query = {
            	'v': 3,
            	'q': {
            		'aggregate': [{
            				'$match': {
            					'$and': [{
            							'$or': [{
            									'blk.t': null
            								},
            								{
            									'blk.t': {
            										'$gte': this.notificationChecked
            									}
            								}
            							]
            						},
            						{
            							'out.b0': {
            								'op': 106
            							}
            						},
            						{
            							'$or': [{
            									'$and': [{
            											'$or': [{
            												'out.h1': '6d03'
            											}, {
            												'out.h1': '6d04'
            											}]
            										},
            										followedSearch
            									]
            								},
            								{
            									'$and': [{
            											'$or': [{
            												'out.h1': '6d06'
            											}]
            										},
            										{
            											'out.h2': '' + userAddressHex + ''
            										},
            										{
            											'in.e.a': {'$ne': this.getAddress()}
            										}
            									]
            								}
            							]
            						}
            					]
            				}
            			},
            			{
            				'$group': {
            					'_id': null,
            					'count': {
            						'$sum': 1
            					}
            				}
            			}
            		]
            	}
            }

            let b64 = Base64.encode(JSON.stringify(query))
            let url = window.bitdbNode + b64

            let header = {
              headers: { key: window.bitdbApiKey }
            }

            axios
              .get(url, header)
              .then(response => {
                this.notificationsCount = 0
                if (response.data.u.length > 0) {
                  this.notificationsCount += response.data.u[0].count
                }
                if (response.data.c.length > 0) {
                  this.notificationsCount += response.data.c[0].count
                }
              })
              .catch(error => {
                console.log(JSON.stringify(error))
              })
          }
        }
      }
  };
</script>

<style>
.user-nav-icon {
  margin-top:10px;
  margin-right: 10px;
  font-size: 32px;
  text-decoration: none;
  color: #657786;
  cursor: pointer;
}
.user-nav-icon-active {
  color: #f57786;
}
.user-nav-icon .badge {
  display: block;
  position: absolute;
  top: 0px;
  right: 3px;
  font-size: 11px;
  line-height: 16px;
  height: 16px;
  padding: 0 5px;
  font-family: Arial, sans-serif;
  color: white;
  background-color: red;
  text-shadow: 0 1px rgba(0, 0, 0, 0.25);
  border: 1px solid;
  border-radius: 10px;
  -webkit-box-shadow: none;
  box-shadow: none;
}

@media screen and (max-width: 800px) {
	.user-nav-icon {
	  display: none;
	}
  .nav-dropdown {
    display: block;
    margin: 0;
  }
  .nav-dropdown-active {
    display: block;
    margin: 0;
  }
  .button-active {
    display: block;
    margin: 0;
  }
}
</style>



