<template>
    <div class="grid">
      <HeaderNav  v-bind:title="'Receive ' + (networkName || 'BSV')"/>

      <div class="s_results form">

        <p style="color:#191;font-size:small" v-if="receivedTxs" v-for="item in receivedTxs.data">
          Transaction received: {{ item.tx.h }}
        </p>

        <p style="color:#911;font-size:small" v-if="walletError">{{ walletError }}</p>

        <p>
          <strong>Address format: </strong>
          <a href="javascript:void(0)" v-bind:class="{ 'option-active': (addressType === 'legacy') }" @click="changeAddressType('legacy')">Bitcoin Address (1G....)</a> |
          <a href="javascript:void(0)" v-bind:class="{ 'option-active': (addressType === 'cashaddr') }" @click="changeAddressType('cashaddr')">cashaddr (qp...)</a>
        </p>

        <qriously :value="address" :size="300" />
        <p>{{ address }}</p>
        <p style="padding-bottom:20px;"></p>
      </div>
      <Footer/>
    </div>

</template>

<script>
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'

export default {
  name: 'WalletReceive',
  mixins: [],
  components: {
    HeaderNav,
    Footer
  },
  data () {
    return {
      address: this.getAddress(),
      addressType: null,
      networkName: window.networkName,
      moneyButtonEnabled: false,
      walletError: sessionStorage.getItem('walletError')
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Receive ' + (this.networkName || 'BSV') + ' Payment'
      }
    }
  },
  mounted () {
    this.viewModeRequired()
    this.getLiveAddressInfo()
    if (window.bitdbAddressFormat === 'cashaddr') {
      this.addressType = 'cashaddr'
    } else if (window.bitdbAddressFormat === 'legacy') {
      this.addressType = 'legacy'
    }
    sessionStorage.removeItem('walletError')
  },
  watch: {
    moneyButtonEnabled: function (val) {
    }
  },
  methods: {
    changeAddressType(addressType) {
      if (addressType === 'cashaddr') {
        if (bchaddr.isCashAddress(this.getAddress())) {
          this.address = this.getAddress()
        } else if (bchaddr.isLegacyAddress(this.getAddress())) {
          this.address = bchaddr.toCashAddress(this.getAddress())
        }
        this.addressType = addressType
      }
      if (addressType === 'legacy') {
        if (bchaddr.isCashAddress(this.getAddress())) {
          this.address = bchaddr.toLegacyAddress(this.getAddress())
        } else if (bchaddr.isLegacyAddress(this.getAddress())) {
          this.address = this.getAddress()
        }
        this.addressType = addressType
      }
    }
  }
}
</script>
<style>
.option-active {
  font-weight: bold;
}
</style>



